const width = 960;

  if(document.body.clientWidth <= width) {
    new Swiper('.mySwiper', {

      breakpointsInverse: true,
      breakpoints: {
        0: {
          spaceBetween: 14,
          slidesPerView: 1.4,
          },
    
          576: {
            spaceBetween: 30,
            slidesPerView: 2,
          },
    
          768: {
            spaceBetween: 24,
            slidesPerView: 3,
          },
          961: {
            spaceBetween: 24,
            slidesPerView: 2,
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            },
          },
          
      },
    
    });
  }



