import SmoothScroll from '/assets/libs/smoothScroll/SmoothScroll.min.js';

import "./scrollup";
import "./header-navigation";
import "./index/index-page";


SmoothScroll({
  // Scrolling Core
  animationTime    : 1000, // [ms]
  stepSize         : 50, // [px]

  // Acceleration
  accelerationDelta : 50,  // 50
  accelerationMax   : 2,   // 3

  // Keyboard Settings
  keyboardSupport   : true,  // option
  arrowScroll       : 50,    // [px]

  // Pulse (less tweakable)
  // ratio of "tail" to "acceleration"
  pulseAlgorithm   : true,
  pulseScale       : 4,
  pulseNormalize   : 1,

  // Other
  touchpadSupport   : true, // ignore touchpad by default
  fixedBackground   : true,
  excluded          : ''
});

