const headerNav = document.querySelector(`.header-navigation`);


if (headerNav) {
  const navLinks = headerNav.querySelectorAll(`.header-navigation__link`);

  const linkHoverHandler = (link) => {
    navLinks.forEach((navLink) => navLink.classList.add(`js-outFocus`));
    link.classList.remove(`js-outFocus`);
    link.classList.add(`js-inFocus`);
  };

  const linkBlurHandler = (link) => {
    navLinks.forEach((navLink) => navLink.classList.remove(`js-outFocus`));
    link.classList.remove(`js-inFocus`);
  };

  if (navLinks.length) {
    navLinks.forEach((link) => {
      link.addEventListener(`mouseenter`, () => linkHoverHandler(link));
      link.addEventListener(`focus`, () => linkHoverHandler(link));
      link.addEventListener(`mouseleave`, () => linkBlurHandler(link));
      link.addEventListener(`blur`, () => linkBlurHandler(link));
    });
  }
}
