import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const animateIndexRooms = () => {
  const indexRooms = document.querySelector(`.index-rooms`);

  if (indexRooms) {
    const roomsGallery = indexRooms.querySelector(`.index-rooms__gallery`);

    if (roomsGallery) {
      const decorativeTexts = indexRooms.querySelectorAll(`.index-rooms__decorative-text`);
      const romsGalleryItems = roomsGallery.querySelectorAll(`.gallery-1__item`);
      const galleryContents = roomsGallery.querySelectorAll(`.gallery-1__item-content`);

      gsap.from(roomsGallery, {
        scrollTrigger: {
          trigger: indexRooms,
          scrub: 3,
          start: `top 80%`,
          end: `top 10%`,
        },
        y: 300,
      });

      if (romsGalleryItems.length && galleryContents.length) {
        romsGalleryItems.forEach((galleryItem, index) => {
          if (!(index % 2)) {
            const indexRoomsTl = gsap.timeline({
              scrollTrigger: {
                trigger: galleryItem,
                start: `top-=50% center+=20%`,
                end: `top-=50% center+=20%`,
                once: true,
              },
              autoRemoveChildren: true,
            });

            const tween = gsap.from(galleryContents[index], {
              width: 0,
              duration: 2,
            });

            const tweenTwo = galleryContents[index + 1] ?
              gsap.from(galleryContents[index + 1], {
                width: 0,
                duration: 2,
              })
              : null;

            indexRoomsTl.add(tween, 0);
            if (tweenTwo) {
              indexRoomsTl.add(tweenTwo, 0.5);
            }
          }

          gsap.to(galleryItem.querySelector(`img`), {
            yPercent: 30,
            ease: "none",
            scrollTrigger: {
              trigger: galleryItem,
              start: "top bottom",
              end: `bottom top`,
              scrub: true
            },
          });
        });
      }

      if (decorativeTexts.length) {
        const decorativeTextTl = gsap.timeline({
          scrollTrigger: {
            trigger: indexRooms,
            start: `bottom bottom+=400px`,
            once: true,
          },
          autoRemoveChildren: true,
        });

        decorativeTextTl
          .from(decorativeTexts[0], {
            y: 500,
            duration: 2,
          })
          .from(decorativeTexts[1], {
            y: 500,
            duration: 2,
            delay: 0.5,
          }, 0);

      }
    }
  }
};


export {
  animateIndexRooms,
};
