import Swiper from '/assets/libs/swiper/swiper-bundle.min.js';
import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const SLIDER_DELAY = 4000;
const SLIDE_TRANSITION_DELAY = 2000;


const animatePhoto = (activePhoto, prevPhoto, progress = 0) => {
  const sliderControls = document.querySelectorAll(`.slider-controls__button`);
  const photoTl = gsap.timeline({
    autoRemoveChildren: true,
  });

  if (prevPhoto !== undefined) {
    prevPhoto.classList.remove(`js-isActive`);
    prevPhoto.classList.add(`js-isPrev`);
  }
  activePhoto.classList.add(`js-isActive`);

  photoTl
    .set(activePhoto.querySelector(`.index-seasonal__img-container`), {
      xPercent: 0,
    })
    .set(activePhoto.querySelector(`img`), {
      scale: 1,
    })
    .to(activePhoto.querySelector(`img`), {
      scale: 1.15,
      duration: (SLIDER_DELAY + SLIDE_TRANSITION_DELAY) / 1000 - progress,
      ease: `none`,
    })

    .to(prevPhoto.querySelector(`.index-seasonal__img-container`), {
      xPercent: 100,
      duration: SLIDE_TRANSITION_DELAY / 1000,
      onStart: () => {
        if (sliderControls.length) {
          sliderControls.forEach((control) => control.classList.add(`js-isAnimateRun`));
        }
      },
      onComplete: () => {
        prevPhoto.classList.remove(`js-isPrev`);

        gsap.set(prevPhoto.querySelector(`.index-seasonal__img-container`), {
          xPercent: 0,
          scale: 1,
        });

        if (sliderControls.length) {
          sliderControls.forEach((control) => control.classList.remove(`js-isAnimateRun`));
        }
      }
    }, -progress);
};


const applySeasonalSlider = () => {
  const indexSeasonal = document.querySelector(`.index-seasonal`);
  const description = indexSeasonal.querySelector(`.index-seasonal__description`);
  const photos = indexSeasonal.querySelectorAll(`.index-seasonal__photos-item`);
  const prevButton = indexSeasonal.querySelector(`.slider-controls__button--prev`);
  const nextButton = indexSeasonal.querySelector(`.slider-controls__button--next`);
  const circleCounter = indexSeasonal.querySelector(`.circle-counter`);
  const sliders = [];
  let forwardElement = null;
  let idleElement = null;

  const createSlider = (sliderContainer) => {
    const slider = new Swiper(sliderContainer, {
      speed: SLIDE_TRANSITION_DELAY,
      navigation: {
        nextEl: nextButton,
        prevEl: prevButton,
        disabledClass: `slider-controls__button--disabled`,
      },
      allowTouchMove: true,
      autoplay: {
        delay: SLIDER_DELAY,
      },
      breakpointsInverse: true,
      breakpoints: {
        0: {
          spaceBetween: 14,
          slidesPerView: 1.4,
          autoplay: {
            disableOnInteraction: true,
          },
          },
    
          576: {
            autoplay: {
              delay: 4000,
              disableOnInteraction: true
            },
            spaceBetween: 30,
            slidesPerView: 2,
          },
        },
    });

    slider.autoplay.stop();
    sliders.push(slider);
  };


  if (description && prevButton && nextButton &&  document.body.clientWidth > 560) {
    createSlider(description);
  }
  if(document.body.clientWidth <= 560) {
    console.log(12)
    new Swiper('.index-seasonal__photos', {

      breakpointsInverse: true,
      breakpoints: {
        0: {
          spaceBetween: 14,
          slidesPerView: 1.4,
          },
    
          576: {
            spaceBetween: 30,
            slidesPerView: 2,
          },
    
          768: {
            spaceBetween: 24,
            slidesPerView: 3,
          },
          
      },
    
    });
  }

  if (circleCounter && sliders.length && photos.length) {
    forwardElement =  circleCounter.querySelector(`#forward`);
    idleElement =  circleCounter.querySelector(`#idle`);
    let isStopAutoplay = false;

    const sliderButtonClickHandler = () => {
      isStopAutoplay = true;
      forwardElement.setAttribute(`fill`, `remove`);
      forwardElement.endElement();
    };

    const circleCounterSlider = new Swiper(circleCounter.querySelector(`.circle-counter__container`), {
      direction: `vertical`,
      speed: SLIDE_TRANSITION_DELAY,
      allowTouchMove: false,
    });

    prevButton.addEventListener(`click`, sliderButtonClickHandler);
    nextButton.addEventListener(`click`, sliderButtonClickHandler);

    sliders[0].on('slideChange', () => {
      const activeSlideIndex = sliders[0].activeIndex;
      const prevSlideIndex = sliders[0].previousIndex;
      const activePhoto = photos[activeSlideIndex];
      const prevPhoto = photos[prevSlideIndex];

      circleCounterSlider.slideTo(activeSlideIndex);

      if (!isStopAutoplay) {
        forwardElement.beginElement();
      }

      /* if (prevSlideIndex !== undefined) {
        prevPhoto.classList.remove(`js-isActive`);
        prevPhoto.classList.add(`js-isPrev`);
      }
      activePhoto.classList.add(`js-isActive`); */
      animatePhoto(activePhoto, prevPhoto);
    });
  }

  return () => {
    if (sliders.length && photos.length) {
      sliders.forEach((slider) => slider.autoplay.start());
      animatePhoto(photos[0], photos[1], SLIDE_TRANSITION_DELAY / 1000);
    }
    if (forwardElement && idleElement) {
      idleElement.beginElement();
    }
  };
};


const animateIndexSeasonal = () => {
  const indexSeasonal = document.querySelector(`.index-seasonal`);

  if (indexSeasonal) {
    const startSliders = applySeasonalSlider();
    let isSlidersStart = false;

    gsap.from(`.index-seasonal__container`, {
      scrollTrigger: {
        trigger: indexSeasonal,
        scrub: 3,
        start: `top 80%`,
        end: `bottom bottom`,
        onUpdate: (self) => {
          if (self.progress > 0.9 && !isSlidersStart) {
            startSliders();
            isSlidersStart = true;
          }
        },
      },
      y: 300,
    });
  }
};


export {
  animateIndexSeasonal,
};
