import Swiper from '/assets/libs/swiper/swiper-bundle.min.js';


const indexAwards = document.querySelector(`.index-awards`);

if (indexAwards) {
  const indexAwardsSlider = indexAwards.querySelector(`.index-awards__slider`);
  const prevButton = indexAwards.querySelector(`.slider-controls__button--prev`);
  const nextButton = indexAwards.querySelector(`.slider-controls__button--next`);

  new Swiper(indexAwardsSlider, {
    speed: 400,
    loop: true,
    slidesPerView: `auto`,
    navigation: {
      nextEl: nextButton,
      prevEl: prevButton,
    },
  });
}
