import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const animateIndexAboutHotel= () => {
  const indexAboutHotel = document.querySelector(`.index-about-hotel`);

  const indexAboutHotelTl = gsap.timeline({
    autoRemoveChildren: true,
    scrollTrigger: {
      trigger: indexAboutHotel,
      start: `top top+=20%`,
      end: `top top+=20%`,
    },
  });

  if (indexAboutHotel) {
    gsap.from(indexAboutHotel.querySelector(`.index-about-hotel__container`), {
      scrollTrigger: {
        trigger: indexAboutHotel,
        scrub: 3,
        start: `top 80%`,
        end: `top 10%`,
      },
      y: 300,
    });

    gsap.to(indexAboutHotel.querySelector(`.index-about-hotel__img-wrapper img`), {
      scrollTrigger: {
        trigger: indexAboutHotel,
        start: `top 80%`,
        end: `top 80%`,
      },
      scale: 1.2,
      duration: 3,
    });

    indexAboutHotelTl
      .from(indexAboutHotel.querySelector(`.index-about-hotel__info-wrapper`), {
        xPercent: -100,
        duration: 2,
      })
      .addLabel(`two`)
      .from(Array.from(indexAboutHotel.querySelectorAll(`.index-about-hotel__info-title`)), {
        opacity: 0,
        duration: 1.5,
      }, `two`)
      .to(Array.from(indexAboutHotel.querySelectorAll(`.index-about-hotel__curtain`)), {
        width: 0,
        duration: 1.5,
      }, `two`)
      .from(indexAboutHotel.querySelector(`.index-about-hotel__info-button`), {
        borderColor: `transparent`,
        opacity: 0,
        duration: 1.5,
      }, `two`);

    gsap.to(indexAboutHotel.querySelector(`.index-about-hotel__img-wrapper img`), {
      yPercent: 30,
      ease: "none",
      scrollTrigger: {
        trigger: indexAboutHotel,
        start: "top center",
        end: `bottom top`,
        scrub: true
      },
    });
  }
};


export {
  animateIndexAboutHotel,
};
