const scrollUp = document.querySelector('.scroll-up');

const windowScrollHandler = (element, scrollValue) => {
  window.addEventListener('scroll', () => {
    if (pageYOffset > scrollValue) {
      element.classList.add('js-isWindowScroll');
    } else {
      element.classList.remove('js-isWindowScroll');
    }
  });
};

if (scrollUp) {
  windowScrollHandler(scrollUp, 500);

  scrollUp.addEventListener(`click`, () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    scrollUp.blur();
  });
}
