import gsap from '/assets/libs/gsap/gsap.min.js';


 // выпадающее меню навигации
function OpenBurgerMenu() {
  const btnBurger = document.querySelector('.header__burgermenu-container');
  const menu = document.querySelector('.header');
  menu.classList.add('show')
  btnBurger.addEventListener('click', () => {
    btnBurger.classList.toggle('open')
    if(!(menu.classList.contains('show'))) {
      menu.classList.add('show')
    } else {
      menu.classList.remove('show')
    }
  })
}
OpenBurgerMenu();

const animateHeader = () => {
  const header = document.querySelector(`.header`);

  if (header) {
    const indexAboutTl = gsap.timeline({
      autoRemoveChildren: true,
    });

    indexAboutTl
      .set(`.header`, {
        delay: 0.5,
        opacity: 1,
        overflow: `hidden`,
      })

      .addLabel(`two`, 1)
      .from(`.header__logo`, {
        duration: 2.5,
        opacity: 0,
      }, `two`)
      .from(`.header__travellers-choise`, {
        duration: 2.5,
        opacity: 0,
      }, `two`)
      .from(`.header__language, .header__pick-up-room-container, .header__socials`, {
        duration: 2,
        opacity: 0,
        yPercent: 100,
        ease: `none`,
      }, `two`)

      .addLabel(`three`, `-=1`)
      .from(`.header-navigation__item`, {
        opacity: 0,
        yPercent: 100,
        stagger: 0.2,
      }, `three`)

      .set(`.header`, {
        overflow: `auto`,
      });
  }
};


export {
  animateHeader,
};
