import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const animateIndexService = () => {
  const indexService = document.querySelector(`.index-service`);

  if (indexService) {
    const galleryItems = indexService.querySelectorAll(`.gallery-2__item`);
    const decorativeTexts = indexService.querySelectorAll(`.index-service__decorative-text`);

    gsap.from(indexService.querySelector(`.index-service__container`), {
      scrollTrigger: {
        trigger: indexService,
        scrub: 3,
        start: `top 80%`,
        end: `top 10%`,
      },
      y: 300,
    });

    if (galleryItems.length) {
      galleryItems.forEach((galleryItem, index) => {
        const galleryItemTl = gsap.timeline({
          autoRemoveChildren: true,
          scrollTrigger: {
            trigger: galleryItem,
            start: `top center+=40%`,
            end: `top center+=40%`,
          },
        });

        galleryItemTl
          .to(galleryItem.querySelector(`.gallery-2__img-curtain`), {
            height: 0,
            duration: 2,
          })
          .to(galleryItem.querySelector(`img`), {
            scale: 1.2,
            duration: 2,
          }, 0)
          .from(galleryItem.querySelector(`.gallery-2__info`), {
            xPercent: (index % 5 === 0) && (index % 10 === 5) ? -100 : 100,
            duration: 1.5,
          }, 0.5);

        gsap.to(galleryItem.querySelector(`img`), {
          yPercent: 20,
          ease: "none",
          scrollTrigger: {
            trigger: galleryItem,
            start: "top center+=30%",
            end: `bottom center`,
            // start: "top bottom", // the default values
            // end: "bottom top",
            scrub: true
          },
        });
      });
    }

    if (decorativeTexts.length) {
      const decorativeTextTl = gsap.timeline({
        scrollTrigger: {
          trigger: indexService,
          start: `top top-=1400px`,
          once: true,
        },
        autoRemoveChildren: true,
      });

      decorativeTextTl
        .from(decorativeTexts[0], {
          xPercent: -150,
          duration: 2,
        });
    }
  }
};


export {
  animateIndexService,
};
