import gsap from '/assets/libs/gsap/gsap.min.js';


const ABOUT_BG_SCALE = 1.03;


const animateAboutBackground = () => {
  const indexAbout = document.querySelector(`.index-about`);

  if (indexAbout) {
    const aboutBg = indexAbout.querySelector(`.index-about__bg`);

    if (aboutBg) {
      indexAbout.addEventListener(`mousemove`, (event) => {
        const documentWidth = document.documentElement.clientWidth;
        const aboutWidth = indexAbout.clientWidth;
        const rotateAngle = (event.clientX - (documentWidth - aboutWidth)) / 150;
        aboutBg.style.transform = `scale(${ABOUT_BG_SCALE}) rotateY(${rotateAngle}deg)`;
      });
    }
  }
};


const animateIndexAbout = () => {
  const indexAbout = document.querySelector(`.index-about`);

  if (indexAbout) {
    const indexAboutTl = gsap.timeline({
      onComplete: animateAboutBackground,
      autoRemoveChildren: true,
    });

    indexAboutTl
      .to(indexAbout.querySelector(`.index-about__curtain`), {
        height: 0,
        ease: `circ.out`,
        duration: 1.2,
      })

      .addLabel(`two`)
      .from(indexAbout.querySelector(`.decorative-section__wrapper`), {
        yPercent: -100,
        duration: 1.8,
        ease: `bounce`,
      }, `two-=0.5`)

      .addLabel(`three`, `-=0.3`)
      .from([...Array.from(indexAbout.querySelectorAll(`.decorative-section__wrapper p`)),
      ...Array.from(indexAbout.querySelectorAll(`.decorative-section__button-wrapper`))], {
        duration: 1.5,
        opacity: 0,
        yPercent: 100,
        ease: `circ.out`,
      }, `three`)
      .from(indexAbout.querySelector(`.decorative-section__decorative-text`), {
        delay: 0.5,
        duration: 2,
        opacity: 0,
      }, `three`)
      .from(`.index-about__aside`, {
        duration: 1.5,
        xPercent: 100,
      }, `three`)
      .from(`.index-about__pick-up-room-container`, {
        duration: 1.5,
        yPercent: 100,
        y: 60,
      }, `three`)
      .to(`.index-about__bg`, {
        duration: 1.5,
        scale: ABOUT_BG_SCALE,
      }, `three`)

      .from(`.index-about__call`, {
        opacity: 0,
        xPercent: 100,
      }, `three+=1`);
  }
};


export {
  animateIndexAbout,
};
