import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const animateIndexTripadvisor = () => {
  const indexTripadvisor = document.querySelector(`.index-tripadvisor`);

  if (indexTripadvisor) {
    gsap.from(indexTripadvisor.querySelector(`.index-tripadvisor__container`), {
      scrollTrigger: {
        trigger: indexTripadvisor,
        scrub: 3,
        start: `top 80%`,
        end: `top 10%`,
      },
      y: 300,
      opacity: 0,
      duration: 2,
    });
  }
};


export {
  animateIndexTripadvisor,
};
