const MAX_DESCRIPTION_LENGTH = 155;

const tripadvisorReviews = document.querySelector(`.index-tripadvisor__reviews`);

if (tripadvisorReviews) {
  const reviews = tripadvisorReviews.querySelectorAll('.index-tripadvisor__reviews-text');

  if (reviews.length) {
    reviews.forEach((review) => {
      let descriptionText = review.textContent;

      if (descriptionText.length > MAX_DESCRIPTION_LENGTH) {
        review.textContent = descriptionText.slice(0, (MAX_DESCRIPTION_LENGTH - 1)) + `...`;
      }
    });
  }
}
