import "./index-awards";
import "./index-tripadvisor-reviews";
import "./index-footer";

import {animateIndexAbout} from "./index-about";
import {animateIndexAboutHotel} from "./index-index-about-hotel";
import {animateIndexRooms} from "./index-rooms";
import {animateIndexSeasonal} from "./index-seasonal";
import {animateIndexService} from "./index-service";
import {animateIndexTripadvisor} from "./index-tripadvisor";
import {animateIndexAwardsContacts} from "./index-awards-contacts";
import {animateFooter} from "../footer";
import {animateHeader} from "../header";


const START_DELAY = 400;

let checkInterval = null;
let isStart = false;

const start = () => {
  animateHeader();
  animateIndexAbout();
  animateIndexService();
  animateIndexSeasonal();
  if(document.body.clientWidth > 525) {
    animateIndexRooms();
    animateIndexTripadvisor();
    animateIndexAwardsContacts();
    animateIndexAboutHotel();
    animateFooter();
  }

};

const checkPreloader = () => {
  if (window.isPreloaderTimerFinish) {
    clearInterval(checkInterval);

    setTimeout(() => {
      if (!isStart) {
        start();
      }
      isStart = true;
    }, START_DELAY);
  }
};

window.addEventListener(`load`, () => {
  if (!checkPreloader()) {
    checkInterval = setInterval(checkPreloader, window.PRELOADER_INTERVAL);
  }
});
