import gsap from '/assets/libs/gsap/gsap.min.js';
import ScrollTrigger from '/assets/libs/gsap/ScrollTrigger.min.js'

gsap.registerPlugin(ScrollTrigger);


const animateIndexAwardsContacts = () => {
  const indexAwardsContacts = document.querySelector(`.index-awards-contacts`);
  const indexAwardsContactsTl = gsap.timeline({
    autoRemoveChildren: true,
    scrollTrigger: {
      trigger: indexAwardsContacts,
      start: `top top`,
      end: `top top`,
    },
  });

  if (indexAwardsContacts) {
    gsap.from(indexAwardsContacts.querySelector(`.index-awards-contacts__awards`), {
      scrollTrigger: {
        trigger: indexAwardsContacts,
        scrub: 3,
        start: `top 80%`,
        end: `top center-=20%`,
      },
      xPercent: -100,
      duration: 2.5,
    });

    indexAwardsContactsTl
      .to(indexAwardsContacts.querySelector(`.index-contacts__curtain`), {
        width: 0,
        duration: 2,
      })
      .addLabel(`two`, `-=1`)
      .to(indexAwardsContacts.querySelector(`.index-contacts__bg`), {
        scale: 1.1,
        duration: 20,
        yoyo: true,
        repeat: -1,
        ease: `none`,
      }, `two`)
      .from(indexAwardsContacts.querySelector(`.decorative-section__decorative-text`), {
        opacity: 0,
        duration: 3,
      }, `two`)
      .from(indexAwardsContacts.querySelector(`.decorative-section__decorative-text`), {
        y: 200,
        duration: 2,
      }, `two`)
      .from(indexAwardsContacts.querySelector(`.decorative-section__wrapper`), {
        yPercent: -100,
        duration: 2,
        ease: `bounce`,
      }, `-=2`)
      .addLabel(`three`, `-=0.5`)
      .from([...Array.from(indexAwardsContacts.querySelectorAll(`.decorative-section__wrapper p`)),
      ...Array.from(indexAwardsContacts.querySelectorAll(`.decorative-section__button-wrapper`))], {
        duration: 2,
        opacity: 0,
        y: 70,
      }, `three`);
  }
};


export {
  animateIndexAwardsContacts,
};
